import { _decorator, Component, Enum, Sprite, SpriteFrame } from 'cc';
import { ESYMBOLID } from './dragonHatchSymbol';
import { tween } from 'cc';

const { ccclass, property } = _decorator;

@ccclass('dragonhatchLine')
export class dragonhatchLine extends Component {
    //从上 顺时针 排布
    @property({ type: [Sprite], tooltip: "" })
    public lines: Sprite[] = [];
    @property({ type: [Sprite], tooltip: "" })
    public rs: Sprite[] = [];
    @property({ type: [SpriteFrame], tooltip: "" })
    public lineFrames: SpriteFrame[] = [];
    @property({ type: [SpriteFrame], tooltip: "" })
    public rFrames: SpriteFrame[] = [];
    onLoad() {

    }

    protected start(): void {
        for (let i = 0; i < this.lines.length; i++) {
            this.lines[i].node.active = true;
            this.rs[i].node.active = true;
        }
    }

    private _symbolId: ESYMBOLID = ESYMBOLID.diamond;
    public get symbolId(): ESYMBOLID {
        return this._symbolId;
    }
    public set symbolId(v: ESYMBOLID) {
        this._symbolId = v;
        let index: number = v - 1;
        for (let i = 0; i < this.lines.length; i++) {
            this.lines[i].spriteFrame = this.lineFrames[index];
            this.rs[i].spriteFrame = this.rFrames[index];
        }
    }

    /**
     * 设置边角可见性
     * nums:上右下左是否有相连symbol
     *  */
    setLinesActive(nums: number[] = [0, 0, 0, 0]) {
        for (let i = 0; i < nums.length; i++) {
            this.lines[i].node.opacity = 0
            this.rs[i].node.opacity = 0
            if (nums[i] == 1) {
                this.lines[i].node.active = false;
                this.rs[i].node.active = false;
                this.rs[(i + 1) % nums.length].node.active = false;
            }
            tween(this.lines[i].node)
                .to(0.5, { opacity: 255 })
                .start();
            tween(this.rs[i].node)
                .to(0.5, { opacity: 255 })
                .start();
        }
    }
}


