import { _decorator, Color, Component, Enum, instantiate, Label, macro, math, Node, Prefab, ProgressBar, sp, Sprite, SpriteFrame, UITransform, Vec3 } from 'cc';
import { dragonhatchHatch, EHATCHTYPE } from './dragonhatchHatch';
import { tween } from 'cc';
import { dragonhatch } from './cDragonhatch';
import { find } from 'cc';
import { Num } from '../../core_tgx/framework/util/Num';
import { Animation } from 'cc';

const { ccclass, property } = _decorator;

@ccclass('infoBoard')
export class infoBoard extends Component {
    @property({ type: Node, tooltip: "消息" })
    public infos: Node = null!;
    @property({ type: Node, tooltip: "中奖金额" })
    public win: Node = null!;
    @property({ type: Animation, tooltip: "" })
    public redLightAni: Animation = null!;
    @property({ type: Node, tooltip: "" })
    public redLight: Node = null!;

    onLoad() {
    }
    private initY: number = 0;

    vibrate() {
        tween(this.node)
            .to(0.1, { y: this.initY - 10 })
            .to(0.1, { y: this.initY + 10 })
            .to(0.1, { y: this.initY })
            .start();
    }

    protected start(): void {
        dragonhatch.infoBoard = this;
        this.redLightAni.on("finished" as any,()=>{
            this.redLight.active = false;
        })
        this.winLev = EWinLev.a;
        this.state = EState.info;
        this.initY = this.node.y;
    }

    public get lab(): Label {
        return find("Label", this.win).getComponent(Label);
    }
    private get curWin(): Node {
        return find("info_win", this.win);
    }
    private get twWin(): Node {
        return find("info_tw", this.win);
    }

    /**显示赢得金币 */
    public playWinAni(coin: number, t: number = 0,twBn:boolean = false) {
        this.state = EState.win;
        this.win.getChildByName("info_win").active = !twBn;
        this.win.getChildByName("info_tw").active = twBn;
        let str: string = Num.exchangeToThousands(dragonhatch.exchangeRate, coin);
        this.lab.string = str;
        tween(this.win)
            .to(t/2, { scale: new Vec3(1.2,1.2) })
            .to(t/2, { scale: new Vec3(1,1) })
            .start();
        this.redLight.active = true;
        this.redLightAni.play();
    }
    
    /**显示消息 */
    public showInfo() {
        this.state = EState.info;
    }

    private _winLev: number = EWinLev.a;
    public get winLev(): number {
        return this._winLev
    }
    public set winLev(v: number) {
        this._winLev = v;
        find("bga", this.node).active = v==EWinLev.a;
        find("bgb", this.node).active = v==EWinLev.b;
        find("bgc", this.node).active = v==EWinLev.c;
    }

    /**获奖金额对应不同背景板 */
    private bindLev(coin: number) {
        if (coin > dragonhatch.betSum * 3 && coin < dragonhatch.betSum * 5) {
            this.winLev = EWinLev.b;
        } else if (coin >= dragonhatch.betSum * 5) {
            this.winLev = EWinLev.c;
        } else {
            this.winLev = EWinLev.a;
        }
    }


    private _state: EState = EState.info;
    public get state(): EState {
        return this._state
    }
    public set state(v: EState) {
        this._state = v;
        this.infos.active = v == EState.info;
        this.win.active = v == EState.win;
        if (this.win.active == false) {
            this.lab.string = "0";
        }
    }
}

export enum EWinLev {
    "a" = 0,
    "b",
    "c",
}

export enum EState {
    "info" = 0,
    "win",
}