import { _decorator, Component, macro, sp } from 'cc';
import { dragonhatch } from './cDragonhatch';
const { ccclass, property } = _decorator;

@ccclass('motherDragon')
export class motherDragon extends Component {
    @property({ type: sp.Skeleton, tooltip: "" })
    public sleep: sp.Skeleton = null!;
    @property({ type: sp.Skeleton, tooltip: "身体" })
    public body: sp.Skeleton = null!;
    @property({ type: sp.Skeleton, tooltip: "爪子" })
    public claw: sp.Skeleton = null!;

    onLoad() {
    }
    protected start(): void {
        this.addListener();
        this.state = ESTATE.sleepIdle;
        dragonhatch.motherDragon = this;
    }

    init() {
        if (this.state == ESTATE.collectIdle) this.state = ESTATE.collectOut;
    }

    /**动画监听 */
    private addListener() {
        this.sleep.setCompleteListener((event) => {
            switch (event.animation.name) {
                case ESLEEP.collectActivate:
                    this.state = ESTATE.collectIdle;
                    break;
                case ESLEEP.collectExit:
                    this.state = ESTATE.sleepIdle;
                    break;
                case ESLEEP.collectToFeature:
                    this.sleepBn = false;
                    this.playAwakeAni(EAWAKE.featureActivate, false);
                    break;
            }
        })
        this.body.setCompleteListener((event) => {
            switch (event.animation.name) {
                case EAWAKE.featureActivate:
                case EAWAKE.stomp:
                case EAWAKE.breathFire:
                    if (event.animation.name == EAWAKE.featureActivate) {
                        this.scheduleOnce(() => {
                            //触发母龙
                            dragonhatch.main.playMomMod();
                        }, 0.5)
                    }
                    this.state = ESTATE.awakeIdle;
                    break;
                case EAWAKE.bigwinPre:
                    this.state = ESTATE.bigwinIdle;
                    break;
                case EAWAKE.bigwinIdle:
                    this.state = ESTATE.bigwinExit;
                    break;
                case EAWAKE.bigwinExit:
                    this.state = ESTATE.awakeIdle;
                    dragonhatch.main.settlement();
                    break;
            }
        })
    }

    private _state: ESTATE = ESTATE.sleepIdle;
    public get state(): ESTATE {
        return this._state
    }
    public set state(v: ESTATE) {
        if (v != ESTATE.sleepIdle && this._state == ESTATE.sleepIdle) {
            this.unschedule(this.radomSleepIdle);
        }
        if (v != ESTATE.awakeIdle && this._state == ESTATE.awakeIdle) {
            this.unschedule(this.radomAwakeIdle);
        }
        if(v==ESTATE.sleepIdle && this.sleepBn==false){
            dragonhatch.main.bigFire.play();
        }
        this._state = v;
        this.sleepBn = v <= ESTATE.awakeIn;
        if (dragonhatch.main.progress) dragonhatch.main.progress.node.active = this.sleepBn;
        if (v == ESTATE.awakeIn) dragonhatch.main.progress.node.active = false;
        switch (v) {
            case ESTATE.sleepIdle:
                this.radomSleepIdle();
                this.schedule(this.radomSleepIdle, 8, macro.REPEAT_FOREVER)
                break;
            case ESTATE.collectIn:
                this.playSleepAni(ESLEEP.collectActivate, false);
                break;
            case ESTATE.collectIdle:
                dragonhatch.main.audio.playMomAwakeLoop();
                this.playSleepAni(ESLEEP.collectIdle);
                break;
            case ESTATE.collectOut:
                dragonhatch.main.audio.playMomAwakeOut();
                this.playSleepAni(ESLEEP.collectExit, false);
                break;
            case ESTATE.awakeIdle:
                this.radomAwakeIdle();
                this.schedule(this.radomAwakeIdle, 8, macro.REPEAT_FOREVER)
                break;
            case ESTATE.awakeIn:
                dragonhatch.main.audio.playMomAwake();
                this.playSleepAni(ESLEEP.collectToFeature, false);
                break;
            case ESTATE.stomp:
                dragonhatch.main.audio.playMomStomp();
                this.playAwakeAni(EAWAKE.stomp, false);
                this.scheduleOnce(() => {
                    dragonhatch.infoBoard.vibrate()
                }, 0.4);
                break;
            case ESTATE.breathFire:
                dragonhatch.main.audio.playMomBreathFire();
                this.playAwakeAni(EAWAKE.breathFire, false);
                break;
            case ESTATE.bigwinPre:
                this.playAwakeAni(EAWAKE.bigwinPre, false);
                break;
            case ESTATE.bigwinIdle:
                this.playAwakeAni(EAWAKE.bigwinIdle, false);
                break;
            case ESTATE.bigwinExit:
                this.playAwakeAni(EAWAKE.bigwinExit, false);
                break;
            default:
                break;
        }
    }

    /**随机待机动画 */
    private radomSleepIdle() {
        let id: number = Math.ceil(Math.random() * 5);
        if (id == 0) id = 1;
        if (id == 1) {
            this.playSleepAni(ESLEEP.idle);
        } else {
            dragonhatch.main.audio.playMomSleepIdle(id);
            this.playSleepAni((ESLEEP.idle + id) as ESLEEP);
        }
    }

    /**随机待机动画 */
    private radomAwakeIdle() {
        let id: number = Math.ceil(Math.random() * 6);
        if (id == 0) id = 1;
        if (id == 1) {
            this.playAwakeAni(EAWAKE.idle);
        } else {
            this.playAwakeAni((EAWAKE.idle + id) as EAWAKE);
        }
    }

    /**龙母是否处于睡眠状态 */
    private _sleepBn: boolean = true;
    public get sleepBn(): boolean {
        return this._sleepBn
    }
    public set sleepBn(v: boolean) {
        this._sleepBn = v;
        this.sleep.node.active = v;
        this.body.node.active = !v;
        this.claw.node.active = !v;
    }

    /**睡眠动画 */
    private playSleepAni(aimStr: ESLEEP, loopBn: boolean = true) {
        this.sleep.setAnimation(0, aimStr, loopBn);
    }

    /**苏醒动画 */
    private playAwakeAni(aimStr: EAWAKE, loopBn: boolean = true) {
        this.body.setAnimation(0, aimStr, loopBn);
        this.claw.setAnimation(0, aimStr, loopBn);
    }
}

export enum ESLEEP {
    collectActivate = "collect_activate",//进度条第四阶段触发
    collectExit = "collect_exit", //进度条第四阶段未达成 退场
    collectIdle = "collect_idle",//进度条第四阶段触发 待机
    collectToFeature = "collect_to_feature",//进度条第四阶段达成 过场
    idle = "idle",//待机
    idle2 = "idle2",//待机
    idle3 = "idle3",//待机
    idle4 = "idle4",//待机
    idle5 = "idle5",//待机
}

export enum EAWAKE {
    bigwinExit = "bigwin_exit",//大奖关闭
    bigwinIdle = "bigwin_idle",//大奖待机
    bigwinPre = "bigwin_pre",//大奖前
    breathFire = "breath_fire",//喷火
    featureActivate = "feature_activate",//触发过场
    idle = "idle",//待机
    idle2 = "idle2",//待机
    idle3 = "idle3",//待机
    idle4 = "idle4",//待机
    idle5 = "idle5",//待机
    idle6 = "idle6",//待机
    stomp = "stomp",//砸
}

export enum ESTATE {
    sleepIdle,//睡眠待机
    collectIn,//第四阶段入场
    collectIdle,//第四阶段待机
    collectOut,//第四阶段退场
    awakeIn,//苏醒入场
    awakeIdle,//苏醒待机
    stomp,//砸触发效果
    breathFire,//喷火
    bigwinPre,//大奖前
    bigwinIdle,//大奖待机
    bigwinExit,//大奖关闭
}
