import { find, Sprite, SpriteFrame } from 'cc';
import { _decorator, Component, Prefab, Node, Label } from 'cc';
import { dragonhatchSlotCtrl } from './dragonhatchSlotCtrl';
import { dragonhatchAudio } from './dragonhatchAudio';
import { Vec3 } from 'cc';
import { tween } from 'cc';
import { dragonhatch, ESTATE } from './cDragonhatch';
import { dragonhatchProgress, EHATCHANI } from './dragonhatchProgress';
import { EHATCHTYPE } from './dragonhatchHatch';
import { dragonHatchSymbol } from './dragonHatchSymbol';
import { Animation } from 'cc';
import { instantiate } from 'cc';
import { gameNet } from '../../module_basic/scripts/NetGameServer';
import { SubGameDef } from '../../module_basic/scripts/SubGameDef';
import { dragonhatchReel } from './dragonhatchReel';
import { ResPlayMatch3 } from '../../module_basic/shared/protocols/public/slots/PtlPlayMatch3';
import { addInfo, dataChange, removeInfo } from './dataChange';
import { RoomMgr } from '../../module_basic/scripts/RoomMgr';
import { UIHelp, UIhelpParam } from '../../module_basic/ui_help/UIHelp';
import { LanguageData } from 'db://assets/module_basic/scripts/language/LanguageData';
import { baseSymbol } from '../../module_basic/ui_slot/match3/baseSymbol';
import { dragonhatchDetail } from './dragonhatchDetail';
import { Button } from 'cc';
import { UserMgr } from '../../module_basic/scripts/UserMgr';
import { Num } from '../../core_tgx/framework/util/Num';
const { ccclass, property } = _decorator;

@ccclass('dragonhatchMain')
export class dragonhatchMain extends Component {
    @property({ type: Prefab, tooltip: "火焰" })
    public firePb: Prefab = null!;
    @property({ type: Prefab, tooltip: "角色详情Pb" })
    public detailPb: Prefab = null!;
    @property({ type: Sprite, tooltip: "幼龙模式光效" })
    public modLight: Sprite = null!;
    @property({ type: [SpriteFrame], tooltip: "幼龙模式光效图片" })
    public modLightSpriteFrame: SpriteFrame[] = [];
    @property({ type: dragonhatchProgress, tooltip: "幼龙模式进度条" })
    public progress: dragonhatchProgress = null!;
    @property({ type: [Animation], tooltip: "水龙模式" })
    public waters: Animation[] = [];
    @property({ type: Node, tooltip: "" })
    public mask: Node = null!;
    @property({ type: Animation, tooltip: "母龙退场大火" })
    public bigFire: Animation = null!;
    @property({ type: Node, tooltip: "slotCtrl" })
    public slotCtrlNode: Node = null!;

    public audio: dragonhatchAudio = null!;
    public delayClick: boolean = false;
    public slotCtrl: dragonhatchSlotCtrl = null!;

    onLoad() {
        dragonhatch.main = this;
        this.slotCtrl = this.slotCtrlNode.getComponent(dragonhatchSlotCtrl) as dragonhatchSlotCtrl;
        this.slotCtrl.initBetContent(dragonhatch.BETNUM, dragonhatch.BET, dragonhatch.LINES);
        this.audio = this.node.getComponent('dragonhatchAudio') as dragonhatchAudio;
        this.modLight.node.active = false;
        this.slotCtrl.node.active = true;
    }

    start() {
        this.slotCtrl.setSpinAnim(0);
        this.slotCtrl.bindBet();
        this.waters[0].on("finished" as any, this.playBlueBoom, this);
        dragonhatch.money = UserMgr.inst.coin;
        this.slotCtrl.lblUserCoin.string = Num.exchangeToThousands(dragonhatch.exchangeRate, dragonhatch.money);
    }

    onCLick(event: TouchEvent, args: any) {
        switch (args) {
            case "roll":
                this.startRoll();
                break;
            case "add":
                this.add();
                break;
            case "dec":
                this.dec();
                break;
            case "help":
                tgx.UIMgr.inst.showUI(UIHelp, null, this, new UIhelpParam("rule"));
                break;
            case "pay":
                tgx.UIMgr.inst.showUI(UIHelp, null, this, new UIhelpParam("payout"));
                break;
            case "lastmoney":
                dragonhatch.lastmoneyPanel.showPanel();
                break;
            case "exitGame":
                this.onBtnExitClicked();
                break;
        }
    }

    onBtnExitClicked() {
        tgx.UIAlert.show(LanguageData.getLangByID("exit_game"), true).onClick(async (ok: Boolean) => {
            if (ok) {
                dragonhatch.clear();
                RoomMgr.inst.exitRoom();
            }
        });
    }

    startRoll() {
        if (this.delayClick || dragonhatch.rollEnable == false) {
            return;
        }
        if (!dragonhatch.auto) {
            this.delayClick = true;
            this.scheduleOnce(() => {
                this.delayClick = false;
            }, 1);
            if (dragonhatch.status == 0) {
                dragonhatch.status = ESTATE.run;
                this.sendRoll();
            } else if (dragonhatch.status == 1) {
                this.slotCtrl.setSpinAnim(3);
                this.stopImmediately(dragonhatch.roundData.handCards,);
            }
        }
    }

    add() {
        if (dragonhatch.auto) {
            return;
        }
        dragonhatch.sumIdx += 1;
        dragonhatch.sumIdx = this.slotCtrl.updateBet(dragonhatch.sumIdx) == 0 ? dragonhatch.sumIdx - 1 : dragonhatch.sumIdx;
        tween(this.slotCtrl.lblCurBet.node)
            .to(0.1, { scale: new Vec3(1.2, 1.2, 1.2) })
            .to(0.1, { scale: new Vec3(1, 1, 1) })
            .start()
    }

    dec() {
        if (dragonhatch.auto) {
            return;
        }
        dragonhatch.sumIdx -= 1;
        dragonhatch.sumIdx = this.slotCtrl.updateBet(dragonhatch.sumIdx) == 0 ? dragonhatch.sumIdx + 1 : dragonhatch.sumIdx;
        tween(this.slotCtrl.lblCurBet.node)
            .to(0.1, { scale: new Vec3(1.2, 1.2, 1.2) })
            .to(0.1, { scale: new Vec3(1, 1, 1) })
            .start()
    }

    startAuto(n: any) {
        if (this.delayClick) {
            return;
        }
        if (dragonhatch.status == 0) {
            dragonhatch.auto = true;
            dragonhatch.autoTimes = n;
            (((this.slotCtrl.Btn_stopAuto as Node).children[0] as Node).getComponent(Label) as Label).string = n + "";
            (((this.slotCtrl.Btn_stopAuto as Node).children[1] as Node).getComponent(Label) as Label).string = n + "";
            if (n > 999) {
                ((this.slotCtrl.Btn_stopAuto as Node).children[0] as Node).scale = new Vec3(0.76, 0.76, 0.76);
                ((this.slotCtrl.Btn_stopAuto as Node).children[1] as Node).scale = new Vec3(0.76, 0.76, 0.76);
            } else if (n > 99) {
                ((this.slotCtrl.Btn_stopAuto as Node).children[0] as Node).scale = new Vec3(0.9, 0.9, 0.9);
                ((this.slotCtrl.Btn_stopAuto as Node).children[1] as Node).scale = new Vec3(0.9, 0.9, 0.9);
            }
            else {
                ((this.slotCtrl.Btn_stopAuto as Node).children[0] as Node).scale = new Vec3(1, 1, 1);
                ((this.slotCtrl.Btn_stopAuto as Node).children[1] as Node).scale = new Vec3(1, 1, 1);
            }
            this.slotCtrl.Btn_stopAuto.active = dragonhatch.auto;
            this.slotCtrl.Btn_start.active = !this.slotCtrl.Btn_stopAuto.active;
            this.scheduleOnce(this.sendRoll, 1.5);
        }
    }

    stopAuto() {
        dragonhatch.auto = false;
        dragonhatch.autoTimes = 0;
    }

    private rollCallBackBn: boolean = false;
    rollCallBack() {
        if (this.rollCallBackBn) {
            return;
        }
        this.rollCallBackBn = true;
        this.scheduleOnce(() => {
            this.rollCallBackBn = false;
        }, 1)
        if (dragonhatch.roundData.winCoin > 0) {
            this.slotCtrl.setSpinAnim(3);
        } else {
            this.slotCtrl.setSpinAnim(2);
        }
        this.scheduleOnce(() => {
            dragonhatch.reel.removeRadom();
            dragonhatch.reel.spawn();
            this.rollCallBackNext();
        }, 1);
    }

    /**滚动完成后操作 */
    private rollCallBackNext() {
        this.showRemove();
    }

    /**展示消除 */
    private showRemove() {
        if (dragonhatch.roundData.modType) {
            this.scheduleOnce(() => {
                this.playMod(dragonhatch.roundData.modType);
            }, 1);
            return;
        }
        if (dragonhatch.momModBn) {
            this.playMomMod();
            return;
        }
        let removes: removeInfo[] = dragonhatch.roundData.removeItems;
        if (removes.length > 0) {
            dragonhatch.main.audio.playSelect();
            for (let i = 0; i < 25; i++) {
                let symbol: dragonHatchSymbol = dragonhatch.reel.getSymbolByIndex(i) as dragonHatchSymbol;
                if (symbol instanceof dragonHatchSymbol) {
                    let remove: removeInfo = removes[i];
                    if (remove) {
                        symbol.playWin(remove.winCoin);
                    } else {
                        symbol.darkBn = true;
                    }
                }
            }
            this.scheduleOnce(this.showDrop, 2);
        } else {
            this.doNext();
        }
    }

    /**展示下落 */
    private showDrop() {
        for (let i = 0; i < 25; i++) {
            let symbol: dragonHatchSymbol = dragonhatch.reel.getSymbolByIndex(i) as dragonHatchSymbol;
            if (symbol instanceof dragonHatchSymbol) symbol.darkBn = false;
        }
        for (let i = 0; i < dragonhatch.winCoinNodes.length; i++) {
            let winNode: Node = dragonhatch.winCoinNodes[i];
            winNode.x += winNode.parent.position.x;
            winNode.y += winNode.parent.position.y + dragonhatch.reel.node.parent.y;
            winNode.parent = this.node;
            tween(winNode)
                .to(0.5, { position: dragonhatch.infoBoard.node.position })
                .call(() => {
                    winNode.destroy();
                })
                .start();
        }
        this.scheduleOnce(this.bindWin, 0.5);
        dragonhatch.reel.drop();
    }

    private bindWin() {
        dragonhatch.infoBoard.playWinAni(dragonhatch.roundData.winCoin, 0.2);
        this.progress.score = dragonhatch.roundData.curProgress;
        dragonhatch.winCoinNodes = [];
        // dragonhatch.infoBoard.
    }

    /**执行下一步操作 */
    public doNext() {
        this.unschedule(this.doNext);
        dragonhatch.roundIndex++;
        if (dragonhatch.roundIndex < dragonhatch.dataLen) {
            this.showRemove();
        } else {
            this.settlement();

        }
    }
    /**单次滚动完成结算 */
    private playRollOver() {
        this.rollComplete();
        this.doAuto();
    }

    doAuto() {
        if (dragonhatch.auto && dragonhatch.autoTimes > 0) {
            this.sendRoll();
        }
    }
    /**滚动完成 */
    private rollComplete() {
        dragonhatch.status = ESTATE.stop;
        this.slotCtrl.setSpinAnim(0);
        this.addDetailListener();
    }

    roll(list: number[]) {
        if (!dragonhatch.auto) {
            dragonhatch.rollEnable = true;
        }
        dragonhatch.status = 1;
        this.scheduleOnce(() => {
            dragonhatch.reel.loopEnd(list, () => { this.rollCallBack(); });
        }, 0.5);
    }

    sendRoll() {
        this.initRoll();
        dragonhatch.autoTimes = dragonhatch.autoTimes > 0 ? dragonhatch.autoTimes - 1 : 0;
        if (dragonhatch.autoTimes == 0) {
            dragonhatch.auto = false;
        }
        this.slotCtrl.setSpinAnim(1);
        let autoTimesLab: Label = this.slotCtrl.Btn_stopAuto.children[0].getComponent(Label) as Label;
        autoTimesLab.string = dragonhatch.autoTimes + "";
        // this.audio.playclickstart();
        if (dragonhatch.autoTimes > 999) {
            this.slotCtrl.Btn_stopAuto.children[0].scale = new Vec3(0.5, 0.5);
        } else if (dragonhatch.autoTimes > 99) {
            this.slotCtrl.Btn_stopAuto.children[0].scale = new Vec3(0.64, 0.64);
        }
        else {
            this.slotCtrl.Btn_stopAuto.children[0].scale = new Vec3(1, 1);
        }
        dragonhatch.reel.loop();
        this.sendLottery();
    }

    firstSymbolOver() {
        dragonhatch.reel.spawn();
        this.addDetailListener();
    }

    private addDetailListener() {
        let symbols: baseSymbol[] = dragonhatch.reel.getSymbols;
        for (let i = 0; i < symbols.length; i++) {
            let symbol: baseSymbol = symbols[i];
            symbol.node.getComponent(Button).interactable = true;
        }
    }

    private removeDetailListener() {
        let symbols: baseSymbol[] = dragonhatch.reel.getSymbols;
        for (let i = 0; i < symbols.length; i++) {
            let symbol: baseSymbol = symbols[i];
            symbol.darkBn = false;
            symbol.node.getComponent(Button).interactable = false;
        }
        let detail: dragonhatchDetail = dragonhatch.detail;
        if (detail) detail.node.active = false;
    }

    addDetail(s: baseSymbol) {
        if (!dragonhatch.rollEnable) return;
        let detail: dragonhatchDetail = dragonhatch.detail;
        if (!detail) {
            detail = instantiate(this.detailPb).getComponent(dragonhatchDetail);
            detail.node.parent = this.node;
            dragonhatch.detail = detail;
        }
        detail.node.active = true;
        let symbols: baseSymbol[] = dragonhatch.reel.getSymbols;
        for (let i = 0; i < symbols.length; i++) {
            let symbol: baseSymbol = symbols[i];
            if (symbol.poolId != s.poolId) {
                symbol.darkBn = true;
            }
        }
        detail.node.position = s.node.position;
        detail.node.y += dragonhatch.reel.node.parent.y;
        detail.init(s.symbolId, s.reelDate.index);
    }

    /**每次滚动初始化场景 */
    initRoll() {
        dragonhatch.motherDragon.init();
        this.progress.init();
        dragonhatch.infoBoard.showInfo();
        this.removeDetailListener();
    }

    async sendLottery() {
        let retSpin = await gameNet.conn.callApi('slots/PlayMatch3', {
            gameId: SubGameDef.SLOTS_DRAGONHATCH,
            bet: dragonhatch.betSum.toString()
        });
        console.log('retSpin is', retSpin);
        this.lotteryBack(retSpin);
    }

    exchange(data: ResPlayMatch3) {
        let res = dataChange.change(data);
        dragonhatch.lotteryRes = res;
    }

    private lotteryBack(data: any) {
        if (data && data.isSucc) {
            this.exchange(data.res as ResPlayMatch3);
            this.scheduleOnce(() => {
                dragonhatch.roundIndex = 0;
                this.roll(dragonhatch.roundData.handCards)
            }, dragonhatch.slotCtrl.isSpeed ? 0.2 : 0.5)
        }
    }

    stopImmediately(arr: number[]) {
        dragonhatch.reel.stopImmediately(arr, () => { this.rollCallBack(); });
    }

    playMod(mod: EMOD) {
        this.progress.playHatchMod(mod);
        switch (mod) {
            case EMOD.green:
                dragonhatch.main.audio.playGreenMod();
                this.playGreenMod();
                this.showModLight(EHATCHTYPE.green)
                break;
            case EMOD.blue:
                dragonhatch.main.audio.playBlueMod();
                this.playBlueMod();
                this.showModLight(EHATCHTYPE.blue)
                break;
            case EMOD.red:
                dragonhatch.main.audio.playRedMod();
                this.playRedMod();
                this.showModLight(EHATCHTYPE.red)
                break;
            case EMOD.yellow:
                this.playYellowMod();
                break;
            default:
                break;
        }
    }
    playGreenMod() {
        let symbols: dragonHatchSymbol[] = dragonhatch.getSymbols();
        for (let i = 0; i < symbols.length; i++) {
            let symbol: dragonHatchSymbol = symbols[i];
            symbol.playGreenMod();
        }
    }
    playBlueMod() {
        for (let i = 0; i < this.waters.length; i++) {
            let ani: Animation = this.waters[i];
            ani.play();
        }
        let reel: dragonhatchReel = dragonhatch.reel;
        (reel.getSymbolByPos(1, 1) as dragonHatchSymbol).playBlueMod();
        (reel.getSymbolByPos(1, 3) as dragonHatchSymbol).playBlueMod();
        (reel.getSymbolByPos(3, 1) as dragonHatchSymbol).playBlueMod();
        (reel.getSymbolByPos(3, 3) as dragonHatchSymbol).playBlueMod();
    }
    playBlueBoom() {
        let reel: dragonhatchReel = dragonhatch.reel;
        (reel.getSymbolByPos(1, 1) as dragonHatchSymbol).hideBlueMod();
        (reel.getSymbolByPos(1, 3) as dragonHatchSymbol).hideBlueMod();
        (reel.getSymbolByPos(3, 1) as dragonHatchSymbol).hideBlueMod();
        (reel.getSymbolByPos(3, 3) as dragonHatchSymbol).hideBlueMod();
    }

    private fires: Node[] = [];
    private H_ITEM = 128; //子节点高度
    private W_ITEM = 150; //子节点宽度
    clearFires() {
        for (let i = 0; i < this.fires.length; i++) {
            this.fires[i].parent = null;
        }
        this.fires = [];
    }
    playRedMod() {
        this.firstFires();
    }
    firstFires() {
        this.clearFires();
        for (let i = 0; i < 3; i++) {
            let fireNode: Node = instantiate(this.firePb);
            fireNode.angle = 180;
            fireNode.x = this.mask.w / 2 + fireNode.w / 2;
            fireNode.y = (i - 1) * 2 * this.H_ITEM;
            fireNode.parent = this.mask;
            this.fires.push(fireNode);
            tween(fireNode)
                .to(0.6, { x: -(this.mask.w / 2 + fireNode.w / 2) })
                .call(this.secondFires, this)
                .start();
        }
        let reel: dragonhatchReel = dragonhatch.reel;
        (reel.getSymbolByPos(4, 0) as dragonHatchSymbol).playRedMod();
        (reel.getSymbolByPos(4, 2) as dragonHatchSymbol).playRedMod();
        (reel.getSymbolByPos(4, 4) as dragonHatchSymbol).playRedMod();
        this.scheduleOnce(() => {
            (reel.getSymbolByPos(2, 0) as dragonHatchSymbol).playRedMod();
            (reel.getSymbolByPos(2, 2) as dragonHatchSymbol).playRedMod();
            (reel.getSymbolByPos(2, 4) as dragonHatchSymbol).playRedMod();
        }, 0.3)
        this.scheduleOnce(() => {
            (reel.getSymbolByPos(0, 0) as dragonHatchSymbol).playRedMod();
            (reel.getSymbolByPos(0, 2) as dragonHatchSymbol).playRedMod();
            (reel.getSymbolByPos(0, 4) as dragonHatchSymbol).playRedMod();
        }, 0.6)
    }
    private secondFiresBn: boolean = false;
    secondFires() {
        if (this.secondFiresBn) return;
        this.secondFiresBn = true;
        this.scheduleOnce(() => {
            this.secondFiresBn = false;
        }, 2)
        this.clearFires();
        for (let i = 0; i < 2; i++) {
            let fireNode: Node = instantiate(this.firePb);
            fireNode.angle = 270;
            fireNode.x = (i * 2 - 1) * this.W_ITEM;
            fireNode.y = (this.mask.h + fireNode.w) / 2;
            fireNode.parent = this.mask;
            this.fires.push(fireNode);
            tween(fireNode)
                .to(0.4, { y: -fireNode.y })
                .call(this.hideRedMod, this)
                .start();
        }
        let reel: dragonhatchReel = dragonhatch.reel;
        (reel.getSymbolByPos(1, 3) as dragonHatchSymbol).playRedMod();
        (reel.getSymbolByPos(3, 3) as dragonHatchSymbol).playRedMod();
        this.scheduleOnce(() => {
            (reel.getSymbolByPos(1, 1) as dragonHatchSymbol).playRedMod();
            (reel.getSymbolByPos(3, 1) as dragonHatchSymbol).playRedMod();
        }, 0.2)
    }
    hideRedMod() {
        this.clearFires();
        tween(this.node)
            .call(() => {
                this.hideReds(4);
            })
            .delay(0.1)
            .call(() => {
                this.hideReds(3);
            })
            .delay(0.1)
            .call(() => {
                this.hideReds(2);
            })
            .delay(0.1)
            .call(() => {
                this.hideReds(1);
            })
            .delay(0.1)
            .call(() => {
                this.hideReds(0);
                this.scheduleOnce(this.playRedModEnd, 1);
            })
            .start();
    }

    private hideReds(index: number) {
        let roles: dragonHatchSymbol[] = dragonhatch.getSymbols();
        for (let i = 0; i < roles.length; i++) {
            let role: dragonHatchSymbol = roles[i];
            if (role.index == index) {
                role.hideRedMod();
            }
        }
    }
    /**巨龙模式 */
    playYellowMod() {
        dragonhatch.momModBn = true;
        dragonhatch.motherDragonTip.showBn = true;
    }

    playMomMod() {
        if (dragonhatch.roundData.stompBn) {
            dragonhatch.reel.removeUnuseAll();
            if (dragonhatch.roundIndex == dragonhatch.lotteryRes.rounds.length - 1) {
                dragonhatch.motherDragon.state = 8;
                return;
            }
            dragonhatch.motherDragon.state = 6;
            this.scheduleOnce(() => {
                let symbols: dragonHatchSymbol[] = dragonhatch.getSymbols();
                let removes: removeInfo[] = dragonhatch.roundData.removeItems;
                for (let i = 0; i < removes.length; i++) {
                    let remove: removeInfo = removes[i];
                    if (remove) {
                        let symbol: dragonHatchSymbol = symbols[remove.index];
                        let nums: number[] = [0, 0, 0, 0];
                        let indexs: number[] = [remove.index + 1, remove.index + 5, remove.index - 1, remove.index - 5]
                        for (let j = 0; j < indexs.length; j++) {
                            let index: number = indexs[j];
                            if (index >= 0 && index < 25) {
                                let s: dragonHatchSymbol = symbols[index];
                                if (s.symbolId == symbol.symbolId) {
                                    nums[j] = 1;
                                }
                            }
                        }
                        symbol.showLines(nums);
                    }
                }

                this.scheduleOnce(() => {
                    dragonhatch.motherDragon.state = 7;
                }, 2);
                this.scheduleOnce(this.stopRadom, 3);
            }, 1)
        } else {
            let removes: removeInfo[] = dragonhatch.roundData.removeItems;
            if (removes.length > 0) {
                for (let i = 0; i < 25; i++) {
                    let symbol: dragonHatchSymbol = dragonhatch.reel.getSymbolByIndex(i) as dragonHatchSymbol;
                    if (symbol instanceof dragonHatchSymbol) {
                        let remove: removeInfo = removes[i];
                        if (remove) {
                            symbol.playWin(remove.winCoin);
                        } else {
                            symbol.darkBn = true;
                        }
                    }
                }
                this.scheduleOnce(this.showDrop, 2);
            } else {
                this.doNext();
            }
        }
    }

    settlement() {
        if (dragonhatch.lotteryRes.winCoin > dragonhatch.betSum * 5) {
            dragonhatch.bigwin.bigwincoin(dragonhatch.lotteryRes.winCoin);
        } else {
            dragonhatch.infoBoard.playWinAni(dragonhatch.lotteryRes.winCoin, 1, true)
            this.scheduleOnce(() => {
                if (dragonhatch.motherDragon.sleepBn == false) dragonhatch.motherDragon.state = 0;
                this.progress.init();
                this.playRollOver();
            }, 1);
        }
    }

    closeBigWin() {
        if (dragonhatch.motherDragon.sleepBn == false) dragonhatch.motherDragon.state = 0;
        this.progress.init();
        dragonhatch.infoBoard.playWinAni(dragonhatch.lotteryRes.winCoin, 0, true)
        this.playRollOver();
    }

    private stopRadom() {
        let symbols: dragonHatchSymbol[] = dragonhatch.getSymbols();
        let removes: removeInfo[] = dragonhatch.roundData.removeItems;
        let adds: addInfo[] = dragonhatch.roundData.addItems;
        let count: number = 0;
        for (let i = 0; i < removes.length; i++) {
            let remove: removeInfo = removes[i];
            if (remove) {
                let add: addInfo = adds[count];
                count++;
                let symbol: dragonHatchSymbol = symbols[remove.index];
                symbol.stopRadom(add.id);
            }
        }
        this.doNext();
    }

    /**幼龙模式光效 */
    showModLight(type: EHATCHTYPE) {
        this.modLight.node.active = true;
        this.modLight.spriteFrame = this.modLightSpriteFrame[type];
    }
    hideModLight() {
        this.modLight.node.active = false;
    }

    private playGreenModEndBn: boolean = false;
    /**土龙模式播放完成 */
    playGreenModEnd() {
        if (this.playGreenModEndBn) return;
        this.playGreenModEndBn = true;
        this.scheduleOnce(() => {
            this.playGreenModEndBn = false;
        }, 2)
        this.hideModLight();
        let removes: removeInfo[] = dragonhatch.roundData.removeItems;
        if (removes.length > 0) {
            this.drop();
        } else {
            this.doNext();
        }
    }

    private playBlueModEndBn: boolean = false;
    playBlueModEnd() {
        if (this.playBlueModEndBn) return;
        this.playBlueModEndBn = true;
        this.scheduleOnce(() => {
            this.playBlueModEndBn = false;
        }, 2)
        this.hideModLight();
        this.doNext();
    }

    private playRedModEndBn: boolean = false;
    playRedModEnd() {
        if (this.playRedModEndBn) return;
        this.playRedModEndBn = true;
        this.scheduleOnce(() => {
            this.playRedModEndBn = false;
        }, 2)
        this.hideModLight();
        this.doNext();
    }

    drop() {
        this.scheduleOnce(() => {
            dragonhatch.reel.drop();
        }, 0.5);
    }
}

export enum EMOD {
    null,
    green,
    blue,
    red,
    yellow
}