import { _decorator, Color, Component, Enum, instantiate, Label, macro, math, Node, Prefab, ProgressBar, sp, Sprite, SpriteFrame, UITransform, Vec3 } from 'cc';
import { dragonhatchHatch, EHATCHTYPE } from './dragonhatchHatch';
import { tween } from 'cc';
import { dragonhatch } from './cDragonhatch';
import { ESTATE } from './motherDragon';

const { ccclass, property } = _decorator;

@ccclass('motherDragonTip')
export class motherDragonTip extends Component {


    onLoad() {
    }
    protected start(): void {
       this.node.active = false;
        dragonhatch.motherDragonTip = this;
    }

    private _showBn:boolean = false;
    
    public get showBn() : boolean {
        return this._showBn;
    }
    
    public set showBn(v : boolean) {
        if(this._showBn==v)return;
        this._showBn = v;
        this.node.active = v;
        this.node.opacity = !v?255:0;
        tween(this.node)
            .to(1,{opacity:v?255:0})
            .call(()=>{
                if(v){
                    this.scheduleOnce(()=>{
                        this.showBn=false;
                    },4);
                }else{
                    dragonhatch.motherDragon.state = ESTATE.awakeIn;
                    dragonhatch.main.audio.playMomAwakeTip();
                }
            })
            .start();
    }
    
}

