

import { Node } from "cc";
import { SubGameDef } from "../../module_basic/scripts/SubGameDef";
import { dragonhatchDetail } from "./dragonhatchDetail";
import { dragonhatchMain } from "./dragonhatchMain";
import { dragonHatchSymbol } from "./dragonHatchSymbol";
import { dragonhatchSlotCtrl } from "./dragonhatchSlotCtrl";
import { motherDragon } from "./motherDragon";
import { motherDragonTip } from "./motherDragonTip";
import { dragonhatchBigwin } from "./dragonhatchBigwin";
import { infoBoard } from "./infoBoard";
import { dragonhatchReel } from "./dragonhatchReel";
import { reelDate } from "../../module_basic/ui_slot/match3/baseReel";
import { baseSymbol } from "../../module_basic/ui_slot/match3/baseSymbol";
import { clientRes, dataChange, Round } from "./dataChange";
import { betPanel } from "./betPanel";
import { autoPanel } from "./autoPanel";
import { lastmoneyPanel } from "./lastmoneyPanel";

class cDragonhatch {
    public static _instance: cDragonhatch | null = null;

    public static get instance(): cDragonhatch {
        if (!this._instance) {
            this._instance = new cDragonhatch();
        }
        return this._instance;
    }

    public BETNUM = dataChange.BETNUM;
    public LINES = dataChange.LINES;
    public BET = dataChange.BET;

    private _isNewUser: boolean = false;
    public get isNewUser(): boolean {
        this._isNewUser = false;
        return this._isNewUser;
    }

    private _money: number = 0;
    public get money(): number {
        return this._money;
    }
    public set money(m: number) {
        this._money = m;
    }

    private _exchangeRate: number = 0;
    public get exchangeRate(): number {
        this._exchangeRate = 1;
        return this._exchangeRate;
    }

    public detail: dragonhatchDetail;
    public main: dragonhatchMain = null!;
    public get slotCtrl(): dragonhatchSlotCtrl {
        return this.main.slotCtrl;
    }
    public bet: number = 0;
    public betNum: number = 0;
    public sumIdx: number = 0;

    /**累计下注计算 */
    private _betSum: number;

    public get betSum(): number {
        if (!this._betSum) {
            this._betSum = Number(localStorage.getItem(SubGameDef.SLOTS_DRAGONHATCH + "betSum"));
        }
        return this._betSum;
    }

    public set betSum(v: number) {
        if (this._betSum != v) {
            localStorage.setItem(SubGameDef.SLOTS_DRAGONHATCH + "betSum", v + "");
        }
        this._betSum = v;
    }

    /**获奖信息 */
    public lotteryRes: clientRes = null!;

    /**自动状态 */
    public auto: boolean = false;
    /**自动次数 */
    public autoTimes: number = 0;
    /**转动状态0：停止，1：转动，2：开始转 */
    public status: number = 0;
    /**中奖数字位置 */
    public winLinePos: number[] = [];
    /**大奖个数 */
    public bigWinNum: number = 0;
    /**摇奖计数 */
    public rollIndex: number = 0;
    public _rollEnable: boolean = true;

    /**龙母*/
    public motherDragon: motherDragon = null!;
    /**龙母提示*/
    public motherDragonTip: motherDragonTip = null!;
    /**信息栏*/
    public infoBoard: infoBoard = null!;

    /**bigwin*/
    public bigwin: dragonhatchBigwin = null!;

    /**下注界面 */
    public betPanel: betPanel = null!;
    /**自动界面 */
    public autoPanel: autoPanel = null!;
    /**余额界面 */
    public lastmoneyPanel: lastmoneyPanel = null!;

    public set rollEnable(v: boolean) {
        this._rollEnable = v;
    }

    public get rollEnable(): boolean {
        return this._rollEnable;
    }

    public roundIndex: number = 0;
    public get roundData(): Round {
        return this.lotteryRes.rounds[this.roundIndex];
    }
    public get nextRoundData(): Round {
        return this.lotteryRes.rounds[this.roundIndex + 1];
    }
    public get dataLen(): number {
        return this.lotteryRes.rounds.length;
    }

    public reel: dragonhatchReel = null!;

    public momModBn: boolean = false;//是否处于母龙模式

    getSymbols(): dragonHatchSymbol[] {
        let arr: dragonHatchSymbol[] = [];
        let reel: dragonhatchReel = this.reel;
        let r: reelDate[] = reel.reelDates;
        for (let i = 0; i < r.length; i++) {
            let symbols: baseSymbol[] = r[i].symbols;
            for (let j = 0; j < symbols.length; j++) {
                let symbol: dragonHatchSymbol = symbols[j] as dragonHatchSymbol;
                arr.push(symbol);
            }
        }
        return arr;
    }

    constructor() {

    }

    private _winCoinNodes: Node[] = [];
    public get winCoinNodes(): Node[] {
        return this._winCoinNodes
    }
    public set winCoinNodes(v: Node[]) {
        if (!v || v.length == 0) {
            for (let i = 0; i < this._winCoinNodes.length; i++) {
                this._winCoinNodes[i].destroy();
            }
        }
        this._winCoinNodes = v;
    }

    clear() {
        this.reel?.clear();
        this.main.node.destroy();
        this.rollEnable = true;
        this.status = ESTATE.stop;
        this.infoBoard = undefined;
        this.bigwin = undefined;
        this.reel = undefined;
        this.detail = undefined;
    }
}

export let dragonhatch = cDragonhatch.instance;

export enum ESTATE {
    stop,
    run,
    start
}