import { _decorator, Component, Enum, sp, Vec3 } from 'cc';
import { EEGG, EHATCHANI } from './dragonhatchProgress';
import { Num } from '../../core_tgx/framework/util/Num';
import { tween } from 'cc';
import { dragonhatch } from './cDragonhatch';

const { ccclass, property } = _decorator;

@ccclass('dragonhatchHatch')
export class dragonhatchHatch extends Component {
    @property({ type: [sp.SkeletonData], tooltip: "幼龙骨骼动画" })
    public hatchDatas: sp.SkeletonData[] = [];

    private get ani(): sp.Skeleton {
        return this.node.getComponent(sp.Skeleton);
    }
    onLoad() {

    }

    protected start(): void {
        this.addListener();
    }

    /**动画监听 */
    private addListener() {
        this.ani.setCompleteListener((event) => {
            switch (event.animation.name) {
                case EHATCHANI.spawn:
                    this.playHatchAni(EHATCHANI.idle)
                    break;
                case EHATCHANI.exit:
                    this.node.parent = null;
                    break;
            }
        })
    }

    private _type: EHATCHTYPE = EHATCHTYPE.green;
    public get type(): EHATCHTYPE {
        return this._type;
    }

    public set type(v: EHATCHTYPE) {
        this._type = v;
        this.ani.skeletonData = this.hatchDatas[v];
        this.playHatchAni(EHATCHANI.spawn, false)
    }

    /**幼龙动画 */
    playHatchAni(aimStr: EHATCHANI, loopBn: boolean = true) {
        this.ani.setAnimation(0, aimStr, loopBn);
    }

    jump(ePos: Vec3) {
        dragonhatch.main.audio.playhatch();
        tween(this.node)
            .to(0.5,{position:ePos})
            .start();
    }
}

export enum EHATCHTYPE {
    green = 0,
    blue,
    red
}


