import { _decorator, Animation, Component, tween} from 'cc';
import { dragonhatch } from './cDragonhatch';

const { ccclass, property } = _decorator;

@ccclass('dragonhatchWaterEffect')
/**
 * 水龙触发添加
 */
export class dragonhatchWaterEffect extends Component {
    @property({ type: Animation, tooltip: "爆炸效果" })
    public boomAni: Animation = null!;
    
    onLoad() {
        
    }

    protected start(): void {
        this.boomAni.on("finished" as any, this.remove, this);
    }

    playBoom(){
        this.boomAni.play();
    }

    closeTween(){
        tween(this.node)
            .to(0.5,{opacity:0})
            .call(this.remove,this)
            .start();
    }

    remove(){
        this.node.parent = null;
        dragonhatch.main.playBlueModEnd();
    }
}


