import { _decorator } from "cc";
import { baseReel, ETYPE, reelDate } from "../../module_basic/ui_slot/match3/baseReel";
import { dragonhatch } from "./cDragonhatch";
import { NodePool } from "cc";
import { baseSymbol } from "../../module_basic/ui_slot/match3/baseSymbol";
import { Vec3 } from "cc";
import { dragonHatchSymbol } from "./dragonHatchSymbol";
import { addInfo, removeInfo } from "./dataChange";
import { tween } from "cc";
import { Logger } from "../../core_tgx/base/log/Logger";
import { EMOD } from "./dragonhatchMain";
import { EventTouch } from "cc";
import { Node } from "cc";

const { ccclass, property } = _decorator;

const FIRSTSYMBOL: number[] = [
    9, 4, 7, 6, 8,
    3, 7, 6, 8, 5,
    7, 6, 8, 5, 7,
    6, 8, 5, 7, 1,
    8, 5, 7, 2, 9
];

@ccclass('dragonhatchReel')
export class dragonhatchReel extends baseReel {
    start() {
        dragonhatch.reel = this;
        this.radomIds = [1, 2, 3, 4, 5, 6, 7, 8, 9];
        this.v = 40;
        this.setReelDate();
        // this.scheduleOnce(this.loop, 1);
        // this.scheduleOnce(() => {
        //     this.loopEnd(FIRSTSYMBOL);
        // }, 3);
    }

    /**设置滚轴排布数据 */
    setReelDate() {
        this.reelDates = [
            { index: 0, type: ETYPE.V, num: 5, pool: new NodePool(baseSymbol), size: 150, headPos: new Vec3(-300, -320), vector: 740, symbols: [], switch: false, hierarchy: true },
            { index: 1, type: ETYPE.V, num: 5, pool: new NodePool(baseSymbol), size: 150, headPos: new Vec3(-150, -320), vector: 740, symbols: [], switch: false, hierarchy: true },
            { index: 2, type: ETYPE.V, num: 5, pool: new NodePool(baseSymbol), size: 150, headPos: new Vec3(0, -320), vector: 740, symbols: [], switch: false, hierarchy: true },
            { index: 3, type: ETYPE.V, num: 5, pool: new NodePool(baseSymbol), size: 150, headPos: new Vec3(150, -320), vector: 740, symbols: [], switch: false, hierarchy: true },
            { index: 4, type: ETYPE.V, num: 5, pool: new NodePool(baseSymbol), size: 150, headPos: new Vec3(300, -320), vector: 740, symbols: [], switch: false, hierarchy: true },
        ]
        this.bindResult(FIRSTSYMBOL);
        dragonhatch.main.firstSymbolOver();
    }

    private disTime: number = 0.5;
    /**启动循环播放 */
    loop() {
        dragonhatch.main.audio.playReelLoop();
        for (let i = 0; i < this.reelDates.length; i++) {
            let r: reelDate = this.reelDates[i];
            let disTime: number = dragonhatch.slotCtrl.isSpeed ? 0 : this.disTime * Math.abs(r.index - 2);
            this.scheduleOnce(() => {
                this.reboundMove(this.reelDates[i]);
            }, disTime);
        }
    }

    private reboundMove(r: reelDate): void {
        this.rebound(r, true, () => {
            this.moveOne(r);
        })
    }

    /**停止循环展示结果 */
    loopEnd(arr: number[], callback: Function = () => { }) {
        if (dragonhatch.slotCtrl.isSpeed) {
            this.v = 60;
        } else {
            this.v = 40;
        }
        if (dragonhatch.slotCtrl.isSpeed == false) {
            for (let i = 0; i < this.reelDates.length; i++) {
                let r: reelDate = this.reelDates[i];

                let disTime: number = this.disTime * Math.abs(r.index - 2);
                this.scheduleOnce(() => {
                    this.bindResultOne(r, arr, () => {
                        let cb: Function = () => {
                            callback();
                            dragonhatch.main.audio.playReelEnd(false);
                        }
                        this.rebound(r, false, cb);
                    })
                }, disTime);
            }
        } else {
            this.bindResult(arr, () => {
                callback();
                dragonhatch.main.audio.playReelEnd(true);
            });
        }
    }

    /**消除中奖之后执行下落 */
    drop() {
        let mod: EMOD = dragonhatch.roundData.modType;
        if (mod == EMOD.green) {
            this.doAdd();
            return;
        }
        let removes: removeInfo[] = dragonhatch.roundData.removeItems;
        if (removes.length > 0) {
            dragonhatch.main.audio.playRemove();
            for (let i = 0; i < removes.length; i++) {
                let remove: removeInfo = removes[i];
                if (remove) {
                    let symbol: dragonHatchSymbol = dragonhatch.reel.getSymbolByIndex(remove.index) as dragonHatchSymbol;
                    if (symbol) {
                        symbol.playExit();
                    } else {
                        Logger.error("消除位置不存在roundIndex：" + dragonhatch.roundIndex + "index:" + remove.index);
                    }

                }
            }
        }
        dragonHatchSymbol.removeCount = 0;
    }

    public doAdd() {
        this.scheduleOnce(this.addItems, 0.1);
    }

    private addItems() {
        this.removeUnuseAll();
        let arr: number[] = [5, 5, 5, 5, 5];
        let addItems: addInfo[] = dragonhatch.roundData.addItems;
        for (let i = 0; i < addItems.length; i++) {
            let addItem: addInfo = addItems[i];
            let r: reelDate = this.reelDates[addItem.rindex]
            let symbol: baseSymbol = this.pushSymbol(r, addItem.id);
            symbol.aimBn = true;
            symbol.bindAimPos(arr[addItem.rindex]);
            symbol.node.y += 50;
            arr[addItem.rindex]++;
        }
        this.scheduleOnce(this.playDrop, 1);

    }

    private playDrop() {
        for (let i = 0; i < this.reelDates.length; i++) {
            let r: reelDate = this.reelDates[i];
            let addSymbol: baseSymbol = this.pushSymbol(r);
            for (let j = 0; j < r.symbols.length; j++) {
                let symbol: baseSymbol = r.symbols[j];
                this.dropAim(symbol);
            }
            this.scheduleOnce(() => { addSymbol.recover() }, 0.5)
        }
        this.scheduleOnce(() => {
            this.spawn();
            dragonhatch.main.doNext();
        }, 1);
    }

    private dropAim(symbol: baseSymbol) {
        if (Math.abs(symbol.aimPos - symbol.node.y) > 1) {
            tween(symbol.node)
                .delay(Math.random() * 0.1)
                .to(0.2, { y: symbol.aimPos })
                .call(()=>{
                    dragonhatch.main.audio.playReelEnd(false);
                })
                .by(0.1, { y: -this.reboundDis }, { easing: 'quartOut' })
                .by(0.1, { y: this.reboundDis }, { easing: 'quartIn' })
                .start();
        }
    }

    getSymbolByPos(rIdx: number, idx: number): dragonHatchSymbol {
        return this.reelDates[rIdx].symbols[idx] as dragonHatchSymbol;
    }

    spawn() {
        let symbols: dragonHatchSymbol[] = this.getSymbols as dragonHatchSymbol[];
        for (let i = 0; i < symbols.length; i++) {
            let symbol: dragonHatchSymbol = symbols[i];
            symbol.wildSpawn();
        }
    }
}
