import { _decorator, Color, Component, Enum, instantiate, Label, macro, math, Node, Prefab, ProgressBar, sp, Sprite, SpriteFrame, UITransform, Vec3 } from 'cc';
import { dragonhatchHatch, EHATCHTYPE } from './dragonhatchHatch';
import { tween } from 'cc';
import { EMOD } from './dragonhatchMain';
import { dragonhatch } from './cDragonhatch';
import { ESTATE } from './motherDragon';

const { ccclass, property } = _decorator;

@ccclass('dragonhatchProgress')
export class dragonhatchProgress extends Component {
    @property({ type: Node, tooltip: "进度条背景" })
    public bg: Node = null!;
    @property({ type: Node, tooltip: "进度条框" })
    public kuan: Node = null!;
    @property({ type: Sprite, tooltip: "" })
    public barBG: Sprite = null!;
    @property({ type: ProgressBar, tooltip: "进度条" })
    public progressBar: ProgressBar = null!;
    @property({ type: Label, tooltip: "进度文本" })
    public progressLab: Label = null!;
    @property({ type: Label, tooltip: "进度目标文本" })
    public aimLab: Label = null!;
    @property({ type: [SpriteFrame], tooltip: "进度条背景" })
    public barBgs: SpriteFrame[] = [];
    @property({ type: Node, tooltip: "金龙身体" })
    public goldDragonBody: Node = null!;
    @property({ type: Node, tooltip: "金龙爪子" })
    public goldDragonClaw: Node = null!;
    @property({ type: Node, tooltip: "金龙头" })
    public goldDragonHead: Node = null!;
    @property({ type: Node, tooltip: "龙蛋" })
    public eggs: Node = null!;
    @property({ type: Node, tooltip: "幼龙容器" })
    public hatchContent: Node = null!;
    @property({ type: Prefab, tooltip: "幼龙预制体" })
    public hatchPB: Prefab = null!;

    private bgWidth: number[] = [320, 340, 380, 460];   //进度条背景宽度
    private kuanWidth: number[] = [340, 360, 400, 480]; //进度条边款宽度
    private barBGWidth: number[] = [512, 532, 572, 652];//进度条宽度
    private barTotal: number[] = [340, 390, 460, 570];  //进度条长度
    private bgColor: string[] = ["#153A09", "#0D2E54", "#470708", "#4d4006"];
    private levs: number[] = [10, 30, 50, 70]
    // private progressLevs: number[] = [0.8, 0.5];
    
    /**当前进度条状态 */
    private _progressType: ETYPE = ETYPE.green;
    public get progressType(): ETYPE {
        return this._progressType;
    }

    public set progressType(v: ETYPE) {
        if(this._progressType != v){
            if(v==ETYPE.yellow){
                dragonhatch.motherDragon.state = ESTATE.collectIn;
            }
        }
        this._progressType = v;
        this.bg.children[1].w = this.bgWidth[v];
        this.kuan.children[1].w = this.kuanWidth[v];
        for (let i = 0; i < this.bg.children.length; i++) {
            let sp: Sprite = this.bg.children[i].getComponent(Sprite);
            sp.color = new Color(this.bgColor[v]);
        }
        this.barBG.spriteFrame = this.barBgs[v];
        this.barBG.node.w = this.barBGWidth[v];
        this.progressBar.totalLength = this.barTotal[v];
        this.progressBar.node.x = -this.barBGWidth[v] / 2;
        // this.hatchContent.x = this.progressBar.totalLength + this.progressBar.node.x;
        if (v) {
            let temp: number = (this.kuanWidth[v] - this.kuanWidth[v - 1]) / 2;
            this.eggs.x = temp;
            this.setGoldDragonPos(this.goldDragonX + temp);
        } else {
            this.eggs.x = 0;
            this.setGoldDragonPos(this.goldDragonX);
        }
        this.progressBar.progress = 0;
    }

    private _progressLev: number = 0;

    public get progressLev(): number {
        return this._progressLev;
    }
    /**根据不同挡位播放不同待机动画 */
    public set progressLev(v: number) {
        if (this._progressLev != v && v) {
            let type: number = Number(this.progressType);
            let temp: string = "";
            if (v == 1) {
                temp = EEGG.idle2;
            } else if (v == 2) {
                temp = EEGG.idle3;
            }
            if (type < ETYPE.yellow) {
                this.playEggAni(this.getEgg(type), this.getEggType(type), temp as EEGG);
            } else {
                this.playGoldDragonAni(temp as EGOLDDRAGON);
            }
        }
        this._progressLev = v;
    }

    private get curAimScore() {
        return this.levs[this.progressType];
    }
    private get preAimScore() {
        if (this.progressType) {
            return this.levs[this.progressType - 1];
        }
        return 0;
    }

    private _curScore: number = 0;

    public get curScore(): number {
        return this._curScore;
    }

    public set curScore(v: number) {
        this._curScore = v;
    }

    private goldDragonX: number;
    setGoldDragonPos(aimX: number) {
        this.goldDragonBody.x = aimX;
        this.goldDragonClaw.x = aimX;
        this.goldDragonHead.x = aimX;
    }
    onLoad() {
    }

    protected start(): void {
        this.addListener();
        this.init();
    }

    public init() {
        dragonhatch.momModBn = false;
        dragonhatch.main.audio.playBgm(0);
        if(this.goldDragonX == undefined)this.goldDragonX = this.goldDragonBody.x;
        this.playGoldDragonAni(EGOLDDRAGON.idle);
        this.progressType = ETYPE.green;
        this.score = 0;
        this.hatchContent.removeAllChildren();
        for (let i = 0; i < this.eggs.children.length; i++) {
            let node: Node = this.eggs.children[i];
            node.active = true;
            if (i == 2) {
                this.playEggAni(node, EEGGTYPE.green, EEGG.idle);
            } else {
                this.playEggAni(node, this.getEggType(2-i), EEGG.idlelineup);
            }
        }
    }

    private get denominator(): number {
        return this.curAimScore - this.preAimScore;
    }

    private _score: number = 0;

    public get score(): number {
        return this._score
    }

    public set score(v: number) {
        if(this._score == v) return;
        dragonhatch.main.audio.playBar(this.progressType==ETYPE.yellow);
        this._score = v;
        if (v >= this.curAimScore) {
            this.playscore(this.curAimScore);
        } else {
            if (this.curScore < v || v == 0) {
                this.playscore(v);
            }
        }
    }

    protected update(dt: number): void {
        let aim: number = this.levs[this.progressType];
        this.progressBar.progress = Number(this.progressLab.string) / aim;
        let num: number = Number(this.progressLab.string);
        let preAim = this.progressType > 0 ? this.levs[this.progressType - 1] : 0;
        let disProgress: number = (num - preAim) / (aim - preAim);
        if (disProgress > 1 / 2) {
            this.progressLev = 2;
        } else if (disProgress > 0) {
            this.progressLev = 1;
        }
    }

    //数字滚动动画
    public playscore(score: number) {
        this.aimLab.string = "/" + this.curAimScore;
        if (score < this.curAimScore) {
            this.doPlayscore(score);
        } else {
            this.doPlayscore(this.curAimScore);
        }
    }

    private doPlayscore(v: number) {
        tween(this.progressLab)
            .to(0.5, { string: v })
            .call(() => {
                let progress: number = Number(this.progressLab.string);
                if (progress == this.levs[ETYPE.yellow]) {
                    this.playGoldDragonAni(EGOLDDRAGON.win, false);
                    this.addGoldDragonAni(EGOLDDRAGON.winidle);
                    this.progressBar.totalLength = 654;
                    return;
                }
                if(progress == this.curAimScore) this.eggBorn();
                if (progress != this.score) {
                    this.scheduleOnce(() => {
                        this.playscore(this.score);
                    }, 1.1)
                }
            })
            .start();
    }

    private eggBorn() {
        let type: number = Number(this.progressType);
        dragonhatch.main.audio.playBgm(type+1);
        switch (this.progressType) {
            //蛋壳破开，幼龙跳出
            case ETYPE.green:
            case ETYPE.blue:
            case ETYPE.red:
                let egg: Node = this.getEgg(type);
                this.playEggAni(egg, this.getEggType(type), EEGG.hatch, false);
                let hatch: dragonhatchHatch = instantiate(this.hatchPB).getComponent(dragonhatchHatch);
                hatch.node.parent = this.node;
                hatch.node.position = new Vec3(egg.x, egg.y + 100);
                hatch.type = Number(this.progressType);
                hatch.jump(new Vec3(-70 + this.hatchContent.x, 164));
                hatch.scheduleOnce(() => {
                    hatch.node.parent = this.hatchContent;
                    hatch.node.y -= 40;
                }, 0.6)
                break;
            default:

                break;
        }
        if (this.progressType < ETYPE.yellow) {
            tween(this.node)
                .delay(1)
                .call(() => {
                    this.progressType++;
                    this.progressBar.progress = 0;
                })
                .delay(0.2)
                .call(() => {
                    this.score = this.score;
                })
                .start();
        }else{
            dragonhatch.main.audio.playMomUnlock();
        }
    }

    //触发幼龙模式
    public playHatchMod(mod: EMOD) {
        if (mod == EMOD.yellow) return;
        let hatch: dragonhatchHatch = this.getHatch(mod);
        hatch.node.parent = null;
    }

    /**动画监听 */
    private addListener() {
        // let ani: sp.Skeleton = this.goldDragonHead.getComponent(sp.Skeleton);
        // ani.setCompleteListener((event) => {
        //     switch (event.animation.name) {
        //         case EGOLDDRAGON.win:
        //             this.playGoldDragonAni(EGOLDDRAGON.winidle)
        //             break;
        //     }
        // })
        for (let i = 0; i < this.eggs.children.length; i++) {
            let node: Node = this.eggs.children[i];
            let ani: sp.Skeleton = node.getComponent(sp.Skeleton);
            ani.setCompleteListener((event) => {
                switch (event.animation.name) {
                    case EEGG.spawn://睁眼动作过场
                        this.playEggAni(this.getEgg(i), this.getEggType(i), EEGG.idle);
                        break;
                    case EEGG.hatch://蛋消失
                        // ani.node.active = false;
                        break;
                }
            })
        }
    }

    /**金龙动画 */
    private playGoldDragonAni(aimStr: EGOLDDRAGON, loopBn: boolean = true) {
        let bodyAni: sp.Skeleton = this.goldDragonBody.children[0].getComponent(sp.Skeleton);
        let clawAni: sp.Skeleton = this.goldDragonClaw.getComponent(sp.Skeleton);
        let headAni: sp.Skeleton = this.goldDragonHead.getComponent(sp.Skeleton);
        bodyAni.setAnimation(0, aimStr, loopBn);
        clawAni.setAnimation(0, aimStr, loopBn);
        headAni.setAnimation(0, aimStr, loopBn);
    }
    /**金龙动画 */
    private addGoldDragonAni(aimStr: EGOLDDRAGON, loopBn: boolean = true) {
        let bodyAni: sp.Skeleton = this.goldDragonBody.children[0].getComponent(sp.Skeleton);
        let clawAni: sp.Skeleton = this.goldDragonClaw.getComponent(sp.Skeleton);
        let headAni: sp.Skeleton = this.goldDragonHead.getComponent(sp.Skeleton);
        bodyAni.addAnimation(0, aimStr, loopBn);
        clawAni.addAnimation(0, aimStr, loopBn);
        headAni.addAnimation(0, aimStr, loopBn);
    }
    /**龙蛋动画 */
    private playEggAni(node: Node, eggType: EEGGTYPE, aimStr: EEGG, loopBn: boolean = true) {
        let ani: sp.Skeleton = node.getComponent(sp.Skeleton);
        ani.setAnimation(0, eggType + aimStr, loopBn);
    }


    private getEggType(index: EHATCHTYPE): EEGGTYPE {
        switch (index) {
            case EHATCHTYPE.green:
                return EEGGTYPE.green;
                break;
            case EHATCHTYPE.blue:
                return EEGGTYPE.blue;
                break;
            case EHATCHTYPE.red:
                return EEGGTYPE.red;
                break;
            default:
                console.log("getEggTypeByIndex error:" + index);
                break;
        }
        return EEGGTYPE.green;;
    }

    private getEgg(index: EHATCHTYPE): Node {
        switch (index) {
            case EHATCHTYPE.green:
                return this.eggs.children[2];
                break;
            case EHATCHTYPE.blue:
                return this.eggs.children[1];
                break;
            case EHATCHTYPE.red:
                return this.eggs.children[0];
                break;
            default:
                console.log("getEggByIndex error:" + index);
                break;
        }
        return undefined;
    }

    private getHatch(mod: EMOD): dragonhatchHatch {
        for (let i = 0; i < this.hatchContent.children.length; i++) {
            let hatch: dragonhatchHatch = this.hatchContent.children[i].getComponent(dragonhatchHatch);
            if (hatch.type == mod - 1) {
                return hatch;
            }
        }
        return null;
    }
}

export enum ETYPE {
    green,
    blue,
    red,
    yellow
}

export enum EHATCHANI {
    spawn = "feat_spawn",//出生
    idle = "feat_idle", //待机
    win = "feat_win", //触发效果
    exit = "feat_exit",//退场
}

export enum EGOLDDRAGON {
    idle = "idle", //待机
    idle2 = "idle2", //待机
    idle3 = "idle3", //待机
    win = "win", //触发过场
    winidle = "win_idle",//触发待机
}

export enum EEGG {
    hatch = "hatch", //蛋消失
    idle = "idle", //待机不动
    idle2 = "idle2", //待机晃动
    idle3 = "idle3", //待机跳动
    idlelineup = "idle_lineup", //待机未破壳
    spawn = "spawn", //睁眼动作
}

export enum EEGGTYPE {
    green = "gr_", //绿色 土龙
    blue = "bl_", //蓝色 水龙
    red = "rd_", //红色 火龙
}

