import { _decorator, Component, macro, sp } from 'cc';
import { dragonhatch } from './cDragonhatch';
import { Node } from 'cc';
import { Num } from '../../core_tgx/framework/util/Num';
import { LanguageSprite } from 'db://assets/module_basic/scripts/language/LanguageSprite';
import { Vec3 } from 'cc';
import { tween } from 'cc';
import { Label } from 'cc';
import { Animation } from 'cc';
import { coinParticle } from './coinParticle';
import { SpriteFrame } from 'cc';
import { Sprite } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('dragonhatchBigwin')
export class dragonhatchBigwin extends Component {
    @property({ type: Animation, tooltip: "过场动画" })
    public fireball: Animation = null!;
    @property({ type: Node, tooltip: "" })
    public aniNode: Node = null!;
    @property({ type: Node, tooltip: "金龙" })
    public dragon: Node = null!;
    @property({ type: Node, tooltip: "圆圈" })
    public circle: Node = null!;
    @property({ type: [SpriteFrame], tooltip: "" })
    public circleSpriteFrames: SpriteFrame[] = [];
    @property({ type: Label, tooltip: "coin label" })
    public coinLabel: Label = null!;
    @property({ type: LanguageSprite, tooltip: "" })
    public bigWinBw: LanguageSprite = null!;
    @property({ type: coinParticle, tooltip: "金币特效" })
    public coinParticle: coinParticle = null!;

    private dragonLev: number[] = [2.2, 2.4, 2.8];   //金龙尺寸档位
    private dragonPosLev: number[] = [50, 50, 0];   //金龙位置y档位
    private circleLev: number[] = [1, 1.2, 1.4];   //圆圈尺寸档位
    private coinLev: number[] = [0, 6, 12];   //金币档位
    onLoad() {
    }
    protected start(): void {
        this.fireball.on("finished" as any, this.playBigWinCoin, this);
        this.init();
        dragonhatch.bigwin = this;
    }
    private init() {
        this.coin = 0;
        this.coinLabel.string = "0";
        this.node.active = false;
        this.aniNode.active = false;
        this.bigWinBw.dataID = "bw";
        this.bigWinBw.node.scale = new Vec3(1, 1);
        this.dragon.scale = new Vec3(this.dragonLev[0], this.dragonLev[0]);
        this.circle.scale = new Vec3(this.circleLev[0], this.circleLev[0]);
        this.coinParticle.createNum = this.coinLev[0];
        this.coinParticle.node.removeAllChildren();
    }

    //大奖数字滚动动画
    private aimCoin: number = 0;
    private addcoin: number = 0;
    private addOnce: number = 0;
    private _playOverBn: boolean = true;
    public get playOverBn(): boolean {
        return this._playOverBn;
    }
    public set playOverBn(v: boolean) {
        this._playOverBn = v;
    }

    private _coin: number = 0;
    public get coin(): number {
        return this._coin;
    }
    public set coin(v: number) {
        this._coin = v;
    }

    bigwincoin(coin: number) {
        dragonhatch.main.audio.playBigwin();
        this.node.active = true;
        this.coin = coin;
        this.fireball.play();
    }

    private playBigWinCoin() {
        let coin = this.coin;
        this.aniNode.active = true;
        dragonhatch.main.unschedule(dragonhatch.main.sendRoll);
        this.node.active = true;
        this.bigBn = false;
        this.superBn = false;
        this.megaBn = false;
        this.aimCoin = coin;
        this.addcoin = 0;
        this.addOnce = this.aimCoin / 100;
        this.playOverBn = false;
        this.coinLabel.string = Num.exchangeToThousands(dragonhatch.exchangeRate, this.addcoin);
        this.schedule(this.coinOneChange, 0.05, macro.REPEAT_FOREVER);
        this.scheduleOnce(this.closeBigWin, 16);
    }

    private bigBn: boolean = false;
    private superBn: boolean = false;
    private megaBn: boolean = false;
    private coinOneChange() {
        this.addcoin += this.addOnce;
        if (this.addcoin >= this.aimCoin) {
            this.playBigWin(this.aimCoin);
            this.playCoinOver();
        } else {
            this.playBigWin(this.addcoin);
            this.coinLabel.string = Num.exchangeToThousands(dragonhatch.exchangeRate, this.addcoin);
        }
    }

    private playCoinOver() {
        this.unschedule(this.coinOneChange);
        dragonhatch.main.audio.playBigwinEnd();
        this.playOverBn = true;
        this.coinLabel.string = Num.exchangeToThousands(dragonhatch.exchangeRate, this.aimCoin);
        this.unschedule(this.closeBigWin);
        this.scheduleOnce(this.closeBigWin, 5);
    }

    private playBigWin(coin: number) {
        if (coin >= dragonhatch.betSum * 35 && this.megaBn == false) {
            this.playAnim_MegaWin();
            this.megaBn = true;
        } else if (coin >= dragonhatch.betSum * 15 && coin < dragonhatch.betSum * 35 && this.superBn == false) {
            this.playAnim_SuperWin();
            this.superBn = true;
        } else if (coin < dragonhatch.betSum * 15 && this.bigBn == false) {
            this.playAnim_BigWin();
            this.bigBn = true;
        }
    }

    onFastBigWin(e: TouchEvent) {
        if (this.playOverBn) {
            this.closeBigWin();
        } else {
            this.playCoinOver();
        }
    }

    //大奖BigWin动画
    private playAnim_BigWin() {
        this.playAni(0);
        this.bigWinBw.dataID = "bw";
        this.circle.getComponent(Sprite).spriteFrame = this.circleSpriteFrames[0];
    }

    //大奖SuperWin动画
    private playAnim_SuperWin(coin: number = 0) {
        this.playAni(1);
        this.bigWinBw.dataID = "mw";
        this.circle.getComponent(Sprite).spriteFrame = this.circleSpriteFrames[1];
    }

    //大奖MegaWin动画
    private playAnim_MegaWin(coin: number = 0) {
        this.playAni(2);
        this.bigWinBw.dataID = "smw";
        this.circle.getComponent(Sprite).spriteFrame = this.circleSpriteFrames[2];
    }

    playAni(lev: number) {
        this.coinParticle.createNum = this.coinLev[lev];
        let aniTime: number = 0.2;
        let add: number = 0.4;
        let temp: number = this.dragonLev[0] + add;
        tween(this.dragon)
            .to(aniTime, { scale: new Vec3(temp, temp),y:this.dragonPosLev[lev] })
            .to(aniTime, { scale: new Vec3(this.dragonLev[lev], this.dragonLev[lev]) })
            .start();
        temp = this.circleLev[0] + add;
        tween(this.circle)
            .to(aniTime, { scale: new Vec3(temp, temp) })
            .to(aniTime, { scale: new Vec3(this.circleLev[lev], this.circleLev[lev]) })
            .start();
        tween(this.coinLabel.node)
            .to(aniTime, { scale: new Vec3(1 + add, 1 + add) })
            .to(aniTime, { scale: new Vec3(1, 1) })
            .start();
        tween(this.bigWinBw.node)
            .to(aniTime, { scale: new Vec3(1 + add, 1 + add), y: 140 })
            .to(aniTime, { scale: new Vec3(1, 1), y: 120 })
            .start();
    }

    closeBigWin() {
        this.unschedule(this.closeBigWin);
        if (this.node.active) {
            this.init();
            this.node.active = false;
            dragonhatch.main.audio.playBigwinOut();
        }
        dragonhatch.main.closeBigWin();
    }
}