import { Label } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { ESYMBOLID } from './dragonHatchSymbol';
import { sp } from 'cc';
import { dragonhatch } from './cDragonhatch';
import { baseSymbol } from '../../module_basic/ui_slot/match3/baseSymbol';
const { ccclass, property } = _decorator;

const GAME_COMBINATIONS: string[][] = [
    ["2", "3", "4", "300"],
    ["3", "5", "6", "400"],
    ["4", "6", "9", "500"],
    ["5", "10", "15", "600"],
    ["10", "15", "30", "800"],
    ["15", "20", "40", "1k"],
    ["20", "30", "50", "5k"],
    ["30", "40", "70", "20k"],
];
@ccclass('dragonhatchDetail')
export class dragonhatchDetail extends Component {
    @property({ type: Node, tooltip: "背景板" })
    public BGNode: Node = null!;
    @property({ type: Node, tooltip: "icons" })
    public slotsNode: Node = null!;
    @property({ type: Node, tooltip: "nums" })
    public numNode: Node = null!;
    @property({ type: [Label], tooltip: "中奖分数" })
    public nums: Label[] = [];
    @property({ type: Node, tooltip: "specialNode" })
    public specialNode: Node = null!;

    init(symbolId: number, reelIndex: number) {
        let idx: number = symbolId - 1;
        for (let i = 0; i < this.slotsNode.children.length; i++) {
            let slot: sp.Skeleton = this.slotsNode.children[i].getComponent(sp.Skeleton);
            if (i == idx) {
                slot.node.active = true;
                slot.setAnimation(0, "idle", true);
            } else {
                slot.node.active = false;
            }
        }
        if (symbolId == ESYMBOLID.wild) {
            this.BGNode.w = 380;
            this.numNode.active = false;
            this.specialNode.active = true;
        } else {
            this.BGNode.w = 302;
            this.numNode.active = true;
            this.specialNode.active = false;
            let strs: string[] = GAME_COMBINATIONS[idx];
            for (let i = 0; i < strs.length; i++) {
                this.nums[i].string = strs[i];
            }
        }
        if (reelIndex <= 2) {
            this.BGNode.x = -81;
            this.BGNode.scale_x = 1;
            this.numNode.x = 95;
            this.specialNode.x = 95;
        } else {
            this.BGNode.x = 81;
            this.BGNode.scale_x = -1;
            this.numNode.x = -180;
            this.specialNode.x = -265;
        }
    }

    onClick() {
        this.node.active = false;
        let symbols: baseSymbol[] = dragonhatch.reel.getSymbols;
        for (let i = 0; i < symbols.length; i++) {
            let symbol: baseSymbol = symbols[i];
            symbol.darkBn = false;
        }
    }

}