import { instantiate } from 'cc';
import { Vec3 } from 'cc';
import { ccenum } from 'cc';
import { CCFloat } from 'cc';
import { Tween } from 'cc';
import { math } from 'cc';
import { tween } from 'cc';
import { Node } from 'cc';
import { _decorator, Component, Prefab } from 'cc';

ccenum(CCFloat);
const { ccclass, property } = _decorator;
@ccclass('coinParticle')
export class coinParticle extends Component {
    @property({ type: Prefab, tooltip: "金币动画" })
    public coinPb: Prefab = null!;
    @property({ type: CCFloat, tooltip: "最大数量" })
    public maxNum: number = 0;
    @property({ type: CCFloat, tooltip: "当次生成数量" })
    public createNum: number = 0;
    @property({ type: CCFloat, tooltip: "生产间隔时间" })
    public disTime: number = 0;
    @property({ type: CCFloat, tooltip: "起始大小" })
    public startScale: number = 0;
    @property({ type: CCFloat, tooltip: "结束大小" })
    public endScale: number = 0;
    @property({ type: CCFloat, tooltip: "飞行时间" })
    public flyTime: number = 0;
    @property({ type: CCFloat, tooltip: "飞行范围w" })
    public wRange: number = 0;
    @property({ type: CCFloat, tooltip: "飞行范围h" })
    public hRange: number = 0;

    onLoad() {

    }

    start() {
    }

    private createTime: number = 0;
    update() {
        if (this.node.active == false) {
            this.init();
            return;
        }
        let curTime: number = new Date().getTime();
        if (this.createTime) {
            let dis: number = curTime - this.createTime;
            if (dis < this.disTime * 1000) {
                return;
            }
        }
        this.create();
        this.createTime = curTime;
    }

    private init() {
        this.createTime = 0;
        for (let i: number = 0; i < this.node.children.length; i++) {
            let coin: Node = this.node.children[i];
            this.recycle(coin);
        }
    }

    private pool: Node[] = [];
    private create() {
        let num: number = this.createNum * math.random();
        for (let i: number = 0; i < this.createNum; i++) {
            this.createOne();
        }
    }

    private createOne() {
        if (this.node.children.length >= this.maxNum) {
            return;
        }
        let coin: Node = this.coinFactory();
        coin.scale = new Vec3(this.startScale, this.startScale);
        coin.x = 0;
        coin.y = 0;
        coin.angle = math.random() * 360;
        let r: number = 360 * math.random();
        let aimPos: Vec3 = new Vec3(this.wRange * Math.cos(r), this.hRange * Math.sin(r))
        let delay: number = math.random();
        tween(coin)
            .delay(delay)
            .call(() => {
                if (this.node) {
                    this.node.addChild(coin);
                } else {
                    Tween.stopAllByTarget(coin);
                    coin.destroy();
                }
            })
            .to(this.flyTime, { scale: new Vec3(this.endScale, this.endScale), position: aimPos })
            .call(() => {
                if (this.node) {
                    this.recycle(coin);
                } else {
                    Tween.stopAllByTarget(coin);
                    coin.destroy();
                }
            })
            .start();
    }

    private coinFactory(): Node {
        if (this.pool.length > 0) {
            return this.pool.shift() as Node;
        } else {
            return instantiate(this.coinPb);
        }
    }

    private recycle(coin: Node) {
        coin.parent = null;
        if (this.pool) {
            this.pool.push(coin);
        }

    }

    protected onDestroy(): void {
        for (let i: number = 0; i < this.node.children.length; i++) {
            Tween.stopAllByTarget(this.node.children[i]);
        }
    }
}