import { _decorator, Component, Enum, Node, Prefab, sp, Sprite, SpriteFrame } from 'cc';
import { dragonhatch } from './cDragonhatch';
import { dragonhatchWaterEffect } from './dragonhatchWaterEffect';
import { instantiate } from 'cc';
import { tween } from 'cc';
import { Vec3 } from 'cc';
import { Animation } from 'cc';
import { baseSymbol, ESHOWTYPE } from '../../module_basic/ui_slot/match3/baseSymbol';
import { Label } from 'cc';
import { Num } from '../../core_tgx/framework/util/Num';
import { dragonhatchLine } from './dragonhatchLine';
import { macro } from 'cc';

const { ccclass, property } = _decorator;

@ccclass('dragonHatchSymbol')
export class dragonHatchSymbol extends baseSymbol {
    @property({ type: Prefab, tooltip: "龙母模式连线" })
    public linesPb: Prefab = null!;
    @property({ type: Prefab, tooltip: "水龙模式" })
    public waterEffectPb: Prefab = null!;
    @property({ type: Prefab, tooltip: "火龙模式" })
    public fireEffectPb: Prefab = null!;
    @property({ type: Prefab, tooltip: "龙母模式火焰爆炸效果" })
    public momfireBoomPb: Prefab = null!;
    @property({ type: Prefab, tooltip: "龙母模式火焰爆炸效果" })
    public winCoinPb: Prefab = null!;

    onLoad() {

    }

    protected start(): void {
        this.addListener();
    }

    /**动画监听 */
    private addListener() {
        this.ske.setCompleteListener((event) => {
            switch (event.animation.name) {
                case EROLEANI.featureexit:
                    this.recover()
                    dragonhatch.main.playGreenModEnd();
                    break;
                case EROLEANI.exit:
                    dragonHatchSymbol.removeCount++;
                    if (dragonHatchSymbol.removeCount >= dragonhatch.roundData.addItems.length)
                        dragonhatch.reel.doAdd();
                    this.recover()
                    break;
            }
        })
    }

    /**土龙模式表现 */
    playGreenMod() {
        this.showType = ESHOWTYPE.ske;
        //所有低赔付符号从卷轴中移除
        if (this.symbolId >= ESYMBOLID.diamond && this.symbolId <= ESYMBOLID.spade) {
            this.playAni(EROLEANI.crack1, false);
            this.addAni(EROLEANI.crack2, false);
            this.addAni(EROLEANI.featureexit, false);
        }
    }
    /**水龙模式表现 */
    private waterNode: Node = null;
    playBlueMod() {
        if (this.waterNode) return;
        let waterEffect: dragonhatchWaterEffect = instantiate(this.waterEffectPb).getComponent(dragonhatchWaterEffect);
        this.waterNode = waterEffect.node;
        this.waterNode.parent = this.node.parent;
        this.waterNode.x = this.node.x;
        this.waterNode.y = this.node.y;
    }

    hideBlueMod() {
        this.symbolId = ESYMBOLID.wild;
        this.wildSpawn();
        if (this.waterNode) {
            this.waterNode.getComponent(dragonhatchWaterEffect).playBoom();
            this.waterNode = null;
        }
    }

    /**火龙模式表现 */
    private fireNode: Node = null;
    playRedMod() {
        if (this.fireNode) return;
        this.fireNode = instantiate(this.fireEffectPb);
        this.fireNode.parent = this.node.parent.parent;
        this.fireNode.x = this.node.x;
        this.fireNode.y = this.node.y;
        // let i: number = this.reelDate.index;
        // let j: number = this.node.getSiblingIndex();
        this.fireNode.opacity = 0;
        tween(this.fireNode)
            .to(0.2, { scale: new Vec3(1.2, 1.2), opacity: 255 })
            .to(0.1, { scale: new Vec3(1.1, 1.1) })
            .start();
    }
    /**隐藏火圈 */
    hideRedMod() {
        if (this.fireNode) {
            this.playMomfireBoom();
        }
    }
    /**母龙火焰爆开 */
    playMomfireBoom() {
        let fireBoomAni: Animation = instantiate(this.momfireBoomPb).getComponent(Animation);
        fireBoomAni.node.parent = this.node;
        fireBoomAni.on("finished" as any, () => {
            if( this.symbolId!=ESYMBOLID.wild) this.symbolId = dragonhatch.nextRoundData.handCards[0];
            fireBoomAni.node.parent = null;
            if (this.fireNode) {
                tween(this.fireNode)
                    .to(0.5, { opacity: 0 })
                    .call(() => {
                        this.fireNode.parent = null;
                        this.fireNode = null;
                    })
                    .start();
            }
        });
        fireBoomAni.play();
    }

    /**滚动停止调用 播放wild出场动画*/
    wildSpawn() {
        if (this.symbolId != ESYMBOLID.wild) return;
        dragonhatch.main.audio.playSpawnWild();
        this.showType = ESHOWTYPE.ske;
        this.playAni(EROLEANI.spawn, false);
        this.addAni(EROLEANI.idle, true);
    }

    /**播放中奖 */
    public winCoin: Node;
    playWin(coin: number) {
        this.showType = ESHOWTYPE.ske;
        this.playAni(EROLEANI.win, false);
        if (this.symbolId != ESYMBOLID.wild) {
            this.addAni(EROLEANI.idle, true);
        } else {
            this.addAni(EROLEANI.winidle, true);
        }
        if (coin) {
            let winCoinLab: Label = instantiate(this.winCoinPb).getComponent(Label);
            this.winCoin = winCoinLab.node;
            this.winCoin.parent = this.node;
            winCoinLab.string = Num.exchangeToThousands(dragonhatch.exchangeRate, coin);
            dragonhatch.winCoinNodes.push(this.winCoin);
        }
    }

    /**中奖退场 */
    playExit() {
        this.playAni(EROLEANI.exit, false);
    }

    unuse(): void {
        super.unuse();
        if(this.lines){
            this.lines.destroy();
            this.lines = undefined;
        }
    }
    private lines:dragonhatchLine;
    /**
     * 显示边框
     * nums:上右下左是否有相连symbol
     *  */
    showLines(nums:number[] = [0,0,0,0]){
        this.lines = instantiate(this.linesPb).getComponent(dragonhatchLine);
        this.lines.node.parent = this.node;
        this.lines.symbolId = this.symbolId;
        this.lines.setLinesActive(nums);
        this.scheduleOnce(()=>{
            this.scheduleOnce(this.playRadom,0.1);
        },0.5);
    }

    /**随机变化 */
    playRadom(){
        this.schedule(this.doRadom,0.1,macro.REPEAT_FOREVER);
    }

    /**随机变化停止 */
    stopRadom(aimId:ESYMBOLID){
        this.unschedule(this.doRadom);
        this.symbolId = aimId;
        if(this.lines){
            this.lines.node.destroy();
            this.lines = undefined;
        }
    }

    private doRadom(){
        this.symbolId = this.momModradomId();
    }
    
    /**随机高阶 */
    private momModradomId(): number {
        let tempIds: number[] = [5, 6, 7, 8, 9];
        let index: number = Math.floor(Math.random() * tempIds.length);
        if (index == tempIds.length) {
            index = tempIds.length - 1;
        }
        let id: number = tempIds[index];
        return id;
    }

    public static removeCount:number=0;

    onClick(e:TouchEvent){
        dragonhatch.main.addDetail(this);
    }
}

export enum ESYMBOLID {
    null,
    diamond,
    club,
    heart,
    spade,
    green,
    blue,
    red,
    mom,
    wild
}

export enum EROLEANI {
    win = "win",                  //中奖过场
    idle = "idle",                //中奖状态待机 wild 普通待机
    exit = "exit",                //退场
    crack1 = "crack01",           //土龙模式  阶段1
    crack2 = "crack02",           //土龙模式  阶段2
    featureexit = "feature_exit", //土龙模式  退场
    spawn = "spawn",              //wild 入场动画
    winidle = "win_idle",         //wild 中奖状态待机
}


